.App {
  font-family: sans-serif;
  text-align: center;
}

.key {
  background: white;
  border-radius: 5px;
  border-color: rgba(0, 0, 0, 0.2);
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.2), 0 3px 10px 0 rgba(0, 0, 0, 0.19);
  width: 100px;
  height: 100px;
  margin: 5px;
  cursor: pointer;
}

.key:hover {
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.1), 0 3px 10px 0 rgba(0, 0, 0, 0.1);
}

.key:active {
  box-shadow: none;
  border-color: rgba(0, 0, 0, 0.4);
}

nav ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  overflow: hidden;
  background-color: #333;
}

nav li {
  float: left;
}

nav li a {
  display: block;
  color: white;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
}

nav li a:hover:not(.active) {
  background-color: #111;
}

nav .active {
  background-color: rgb(4, 170, 109);
}

.progress-outer {
  background-color: #e7e7e7;
  margin: 10px;
  padding: 5px;
}

.progress {
  background-color: rgb(4, 170, 109);
}

.word {
  font-size: 35px;
  align-items: center;
  justify-content: center;
  display: flex;
  margin: 0;
  font-weight: bold;
  border: 4px solid rgb(4, 170, 109);
  background-color: rgba(4, 170, 109, 0.2);
  height: 75px;
  width: 300px;
  margin: 10px auto;
}
